<template>
	<div id="search" :class='value?"bgWhite":""'>
		<header>
			<img src="~img/images/chat/left.png" alt="" @click='$router.go(-1)'>
			<van-cell-group>
			  <van-field v-model="value" placeholder="搜索" clearable ref='text' @input='search'/>
			</van-cell-group>
		</header>
		<div v-show='value===""'>
			<p class="searchCon">搜索指定内容</p>
			<ul>
				<li>朋友圈</li>
				<li class="isBorder">文章</li>
				<li>公众号</li>
				<li>小程序</li>
				<li class="isBorder">音乐</li>
				<li>表情</li>
			</ul>
		</div>
		<div  class='haveValue' v-show='userResult.length>0 && value!=""' :class='userResult.length>0 && value?"haveTop":""'>
			<p class='title'>联系人</p>
			<ul class='user'>
				<li v-for='(val,index) in userResult' @click='goUserInfo(val)'>
					<img :src="val.avator" alt="">
					<div>
						<span class='username'>{{val.name}}</span>
						<span class='wechatNum'>微信号: {{val.wechatNum}}</span>
					</div>
				</li>
				<li>
					<img src="~img/images/header/search.png" alt="" class='searchPng' >更多联系人
				</li>
			</ul>
		</div>
		<div class='haveValue' v-show='chatResult.length>0 && value!=""' :class='userResult.length===0 && chatResult.length>0 && value?"haveTop":""'>
			<p class='title'>聊天记录</p>
			<ul class='user chat'>	
				<li v-for='(val,index) in chatResult'>
					<img :src="val.avator" alt="">
					<div>
						<span class='username'>{{val.name}}</span>
						<span class='wechatNum'>1条相关的聊天信息</span>
					</div>
				</li>
				<li>
					<img src="~img/images/header/search.png" alt="" class='searchPng' >更多聊天记录
				</li>
			</ul>
		</div>
		<div class='haveValue' v-show='collectionResult.length>0 && value!=""' :class='userResult.length===0 && chatResult.length===0 &&  collectionResult.length>0 && value?"haveTop":""'>
			<p class='title'>收藏</p>
			<ul class='user collection'>	
				<li v-for='(val,index) in collectionResult'>
					<div>
						<span class='username'>{{val.content}}</span>
						<span class='wechatNum'>来自 {{val.author}}</span>
					</div>
				</li>
				<li>
					<img src="~img/images/header/search.png" alt="" class='searchPng' >更多收藏
				</li>
			</ul>
		</div>
		<!-- v-show=(userResult.length>0 || collectionResult.length>0 || chatResult.length>0) &&  -->
		<div class='phoneQQ' v-show='value.length<15 && value!=""' :class='userResult.length===0 && collectionResult.length===0 && chatResult.length===0 &&value?"haveTop":""'>
			<i></i>查找手机/QQ号：<span>{{value}}</span>
		</div>
		<div class='phoneQQ sou' v-show='value!=""' :class='userResult.length===0 && collectionResult.length===0 && chatResult.length===0 &&value.length>=15?"haveTop":""'>
			<i></i>
			<div>
				搜一搜 <span> {{value}}</span><br>
				<span class='desc'>小程序、公众号、文章、朋友圈和表情等</span>
			</div>	
		</div>
	</div>
</template>
<script>
	import chatList from '../assets/json/chatList'
	import collection from '../assets/json/collectionList'
	export default{
		data(){
			return{
				value:'',
				user:chatList.data,
				userResult:'',
				collection:collection.data,
				collectionResult:'',
				chatResult:'',
				isChinese:false
			}
		},
		methods:{
			search(){
				if(this.value!==''){
					if(/^[\u4e00-\u9fa5]+$/i.test(this.value)){
		                this.isChinese=true;
			        }
			        else{
			            this.isChinese=false;
			        }     
					let userResult=this.user.filter((val)=>{
						if(val.nickName){
							return val.nickName.match(this.value) || val.name.match(this.value) ||  val.wechatNum.match(this.value);
						}
						else{
							return val.name.match(this.value) ||  val.wechatNum.match(this.value);
						}
						
					})
					let collectionResult=this.collection.filter((val)=>{
						if(val.content){
							return val.content.match(this.value);
						}
						
					})
					let chatResult=this.user.filter((val)=>{
						if(val.say){
							return val.say.match(this.value);
						}
						
					})
				
					this.userResult=userResult;
					this.chatResult=chatResult;
					this.collectionResult=collectionResult;
						
				}		
			},
			goUserInfo(val){
				sessionStorage.setItem('nowChatUser',val.name)
					sessionStorage.setItem('nowCuserSay',val.say)
					sessionStorage.setItem('nowUserAvator',val.avator)
					if(val.nickName){
						sessionStorage.setItem('nowChatNickname',val.nickName)
					}
					else{
						sessionStorage.setItem('nowChatNickname','')
					}
					sessionStorage.setItem('nowChatNum',val.wechatNum)
					sessionStorage.setItem('nowUserSex',val.sex)
					sessionStorage.setItem('nowUserArea',val.area)
					sessionStorage.setItem('nowChatTime',val.time)
					this.$router.push('/userInfo');
			}
		},
		computed:{
			filterValue(){
	           
			}
		},
		activated(){
			this.$refs.text.focus();
		},
		beforeRouteLeave (to, from, next) {
			if(to.name!=='userInfo'){
				this.value='';
				next();	
		    }	
		    else{
		    	next();
		    }	
		},
	}
</script>
<style scoped>
	#search{
		background: #ededed;
		height: 100vh;
		overflow-x: hidden;
	}
	#search.bgWhite{
		background: #fff;
	}
	header{
		background: #ededed;
		border-bottom:1px solid #e5e5e5;
		position: fixed;
		width: 100%;
		z-index:666;
		top:0;
		display: flex;
    	align-items: center;
	}
	header>img{
		width: .46rem;
	    height: .46rem;
	    margin-left: .2rem;
	}
	header .van-cell-group{
		width: 92%;
		margin:.1rem;
	} 
	.van-cell{
		padding: .1rem .2rem;
		background-color: #ededed !important;
	}
	header >>> .van-field__control{
	    height: 76%;
	    outline: none;
	    border: none;
	    color: #000;
	    font-size: .38rem !important;
	}
	.searchCon{
		font-size: .36rem;
		text-align: center;
		padding-top: 1.4rem;
		color: #b1b1b1;
		letter-spacing: 1px;
	}
	ul{
		font-size: .36rem;
	    display: flex;
	    align-items: center;
	    flex-wrap: wrap;
		width: 85%;
	    text-align: center;
	    margin: 0 auto;
	    margin-top: .35rem;
	}
	ul li{
		width: 33%;
	    margin: .12rem 0 .52rem 0;
	    color: #586b92;
	}
	ul li.isBorder{
		border-right: 1px solid #dadada;
		border-left: 1px solid #dadada;
	}
	.haveValue{
		background: #fff;
	}
	.noTop{
		padding-top: 0;
	}		
	.haveValue .title{
		font-size: .37rem;
		color:#808080;
		padding: .2rem;
		padding-left: 0;
		margin-left: .3rem;
		border-bottom: 1px solid #dcdcdc;
	}
	ul.user{
		width: 100%;
		margin-top:.1rem;
		padding-left:.36rem;
		border-bottom: .2rem solid #ededed;
	}
	.user li{
		width: 100%;
	    text-align: left;
	    display: flex;
	}
	.user li>div{
		display: flex;
    	flex-direction: column;
    	margin-left: .4rem;
    	width: 100%;
    	border-bottom:1px solid #dcdcdc;
    	padding-bottom: .12rem;
	}
	.user li img{
		border-radius: .13rem;
		width: .9rem;
		height: .9rem;
	}
	.user li:nth-last-child(2){
		border-bottom:1px solid #dcdcdc;
	}
	.user li:nth-last-child(2)>div{
		border-bottom:none;
	}
	.user .username{
		color:#000;
		font-size: .38rem;
		margin-bottom: .05rem;
	}
	.user .wechatNum{
		color:#959595;
		font-size: .34rem;
	}
	.user li .searchPng{
		width: .48rem;
		height: .48rem;
		margin-right: .28rem;
	}
	.user li:last-child{
		align-items: center;
		margin: .1rem 0 .3rem 0;
		background: #fff url('~img/images/right.png') no-repeat 86vw center/.44rem;
	}
	.collection{
		padding-left: 0;
	}
	.collection li div{
		margin-left:0;
	}
	.collection li:last-child{
		padding-left: 0;
	}
	.collection span{
		width: 89%;
		overflow : hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
	}
	.phoneQQ{
		font-size: .38rem;
		padding: .25rem;
		padding-left: .36rem;
		background: #fff;
		display: flex;
		align-items: center;
		border-bottom: .2rem solid #ededed;
	}
	.sou{
		border-bottom:none;
	}
	.haveTop{
		padding-top:1.1rem;
	}
	.phoneQQ>i{
		display: inline-block;
		width: .87rem;
		height: .87rem;
		background: #2da247 url('~img/images/add-user.png') no-repeat center/79%;
		border-radius: .13rem;
		margin-right: .2rem;
	}
	.sou>i{
		background:#fff url('~img/images/sou.png') no-repeat center/98%;
		margin-right: .38rem;
	}
	.sou .desc{
		font-size: .34rem;
		color: #959595;
	}
	.phoneQQ span{
		width: 40%;
	    overflow: hidden;
	    text-overflow: ellipsis;
	    white-space: nowrap;
		color:#15b26b;
	}
	.sou span:nth-of-type(1){
		display: inline-block;
		width: 67%;
		overflow: hidden;
	    text-overflow: ellipsis;
	    white-space: nowrap;
	}
</style>


